import React, { useState } from "react";
import styled from "styled-components";
import table from "../../assets/table.jpg";
import { Layer } from "../UI/Layer";
import projectsJson from "../../assets/projects.json";

const Div = styled.div`
  height: 800px;
  width: 100%;
  position: relative;
  overflow: hidden;

  background-image: url(${table});
  background-size: cover;

  button {
    position: relative;
    z-index: 1000;
  }
`;

const Container = styled.div`
  display: flex;
  overflow: hidden;
  flex-wrap: wrap;
`;

const Block = styled.div`
  display: flex;
  height: 400px;
  flex-direction: column;
  overflow-y: auto;
  padding: 8px;
  width: 33.333%;
  border: 1px solid #1c448e;
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  transition: all 0.3s ease;
  top: ${({ top }) => (top || 0) + "px"};
  left: ${({ left }) => left || 0};
  transform: rotate(${({ angle }) => (angle || 0) + "deg"});
  color: white;
  cursor: pointer;

  &:hover {
    background-color: black;
    z-index: 100;
    transform: rotate(0);
  }

  a,
  h1 {
    text-align: center;
  }

  a {
    padding: 16px;
    transition: all 0.5s linear;
  }

  a:hover {
    background-color: #1c448e;
    color: white;
  }

  @media (max-width: 769px) {
    h1 {
      font-size: 1.25rem;
    }
    p {
      font-size: 0.75rem;
      overflow: hidden;
    }
    a {
      font-size: 0.75rem;
    }
  }
`;

const Tag = styled.p`
  padding: 4px 8px;
  margin: 4px;
  border-radius: 160px;
  background-color: #1c448e;
  display: inline-block;

  @media (max-width: 769px) {
    padding: 2px;
    font-size: 7px;
    margin: 0;
  }
`;

const Projects = () => {
  const [expanded, setExpanded] = useState(true);

  return (
    <Div id="projects">
      <Layer />
      <Container>
        {projectsJson.map((el, i) => (
          <Block
            onClick={() => setExpanded(!expanded)}
            top={expanded ? (400 / 3) * (i - (i % 3)) : 20 * i}
            left={expanded ? (i % 3) * 33.333 + "%" : 20 * i + "px"}
            angle={expanded ? Math.random() * 45 - 22.5 : 0}
          >
            <h1>{el.title}</h1>
            <a
              onClick={(e) => e.stopPropagation()}
              target="_blank"
              rel="noreferrer"
              href={el.link}
            >
              more info
            </a>
            <div>
              {el.tags?.map((t) => (
                <Tag>{t}</Tag>
              ))}
            </div>
            <p>{el.description}</p>
          </Block>
        ))}
      </Container>
    </Div>
  );
};

export default Projects;
